import { calculateDistance } from "@/utils/amap";

export default function (api) {
  return {
    state: {
      cargo: {
        weight: null,
        volume: null,
        type: null
      },
      vehicle: {
        length: "13",
        type: null
      },
      paymentMethod: "",
      consignor: {
        name: "",
        phone: ""
      },
      consignee: {
        name: "",
        phone: ""
      },
      notes: [],
      remark: null
    },
    mutations: {
      setOrderId(state, value) {
        state.id = value;
      },
      setOrderNo(state, value) {
        state.no = value;
      },
      setConsignor(state, data) {
        state.consignor = data;
      },
      setConsignee(state, data) {
        state.consignee = data;
      },
      setNotes(state, notes) {
        state.notes = notes;
      },
      setRemark(state, remark) {
        state.remark = remark;
      },
      setDistance(state, value) {
        state.distance = value;
      },
      clearDistance(state, value) {
        state.distance = null;
      },
      setCargoWeight(state, value) {
        state.cargo.weight = value;
      },
      setCargoVolume(state, value) {
        state.cargo.volume = value;
      },
      setCargoType(state, value) {
        state.cargo.type = value;
      },
      setVehicleLength(state, value) {
        state.vehicle.length = value;
      },
      setVehicleType(state, value) {
        state.vehicle.type = value;
      },
      setPaymentMethod(state, value) {
        state.paymentMethod = value;
      },
    },
    actions: {
      calcDistance: async ({ commit, state }) => {
        let distance = await calculateDistance(
            [state.consignor.lng, state.consignor.lat], [state.consignee.lng, state.consignee.lat]);
        commit("setDistance", distance);
      },
      
      async saveCargoSource({ commit, state }) {
        let orderData = { ...state };
        orderData.origin = state.consignor.adcode;
        orderData.destination = state.consignee.adcode;
        console.log("saveCargoSource", orderData);
        let cargoSource = await api.post("/my/cargo-sources", orderData).then(res => res.data);
        console.log("cargoSource", cargoSource);
        return cargoSource;
      },
  
      async submitOrder({ commit, state }) {
        let orderData = { ...state };
        orderData.origin = state.consignor.adcode;
        orderData.destination = state.consignee.adcode;
        console.log("submitOrder", orderData);
        let order = await api.post("/my/orders", orderData).then(res => res.data);
        console.log("order", order);
        commit("setOrderId", order.id);
        commit("setOrderNo", order.no);
        return order;
      },

    }
  }
}