export async function searchNearBy(center) {
  return new Promise(resolve => {
    AMap.service(["AMap.PlaceSearch"], () => {
      //构造地点查询类
      var placeSearch = new AMap.PlaceSearch({
        type:
          "汽车服务|餐饮服务|购物服务|生活服务|体育休闲服务|医疗保健服务|住宿服务|风景名胜|商务住宅|政府机构及社会团体|科教文化服务|交通设施服务|金融保险服务|公司企业|地名地址信息", // 兴趣点类别
        pageSize: 30, // 单页显示结果条数
        pageIndex: 1, // 页码
        city: "全国", // 兴趣点城市
        autoFitView: true // 是否自动调整地图视野使绘制的 Marker点都处于视口的可见范围
      });
      //根据地图中心点查附近地点
      placeSearch.searchNearBy("", center, 200, (status, result) => {
        resolve(result);
      });
    });
  });
}
export async function search(keyword) {
  return new Promise(resolve => {
    AMap.service(["AMap.PlaceSearch"], () => {
      //构造地点查询类
      var placeSearch = new AMap.PlaceSearch({
        type: "政府机构及社会团体|政府机构及社会团体|公司企业|地名地址信息", // 兴趣点类别
        // "汽车服务|餐饮服务|购物服务|生活服务|体育休闲服务|医疗保健服务|住宿服务|风景名胜|商务住宅|政府机构及社会团体|科教文化服务|交通设施服务|金融保险服务|公司企业|地名地址信息"
        pageSize: 30, // 单页显示结果条数
        pageIndex: 1, // 页码
        city: "全国", // 兴趣点城市
        citylimit: false, //是否强制限制在设置的城市内搜索
        autoFitView: false // 是否自动调整地图视野使绘制的 Marker点都处于视口的可见范围
      });
      //关键字查询
      
      placeSearch.search(keyword, (status, result) => {
        resolve(result);
      });
    });
  });
}

export async function getAddress(location) {
  return new Promise(resolve => {
    AMap.plugin("AMap.Geocoder", function() {
      var geocoder = new AMap.Geocoder({
        // city 指定进行编码查询的城市，支持传入城市名、adcode 和 citycode
        city: "全国"
      });

      var lnglat = [location.lng, location.lat];

      geocoder.getAddress(lnglat, function(status, result) {
        resolve(result);
      });
    });
  });
}

export async function calculateDistance(origin, destination) {
  return new Promise(resolve => {
    AMap.plugin("AMap.Driving", function() {
      var driving = new AMap.Driving({
        // 驾车路线规划策略，AMap.DrivingPolicy.LEAST_TIME是最快捷模式;  LEAST_FEE是最经济模式;  LEAST_DISTANCE是最短距离模式;   REAL_TRAFFIC是考虑实时路况模式
        policy: AMap.DrivingPolicy.REAL_TRAFFIC
        //
      });

      var startLngLat = origin;
      var endLngLat = destination;

      driving.search(startLngLat, endLngLat, function(status, result) {
        let distance = Math.round(result.routes[0].distance / 1000);
        resolve(distance);
      });
    });
  });
  
}

