import Vue from 'vue'
import AMap from 'vue-amap'
import VueTouch from 'vue-touch'
import Vuex from "vuex";
import { Toast } from "mint-ui";
import routes from "@/routes/index";
import App from './App.vue'


// 要么在全局注册组件，组件中直接调用，要么在组件中引入并调用
// import { MessageBox } from "mint-ui";
// Vue.use(MessageBox);

import { initStore } from '@/store'

import 'lib-flexible/flexible.js'
import 'font-awesome/css/font-awesome.css'
import "mint-ui/lib/style.css"
import vueWechatTitle from "vue-wechat-title"

import 'iview/dist/styles/iview.css'
import {
  Upload
} from 'iview';
import { Popup } from 'mint-ui';
import { toCityName, toDistrictName } from './utils';
import VueRouter from 'vue-router';
Vue.component('Upload', Upload);
Vue.component(Popup.name, Popup);

Vue.use(vueWechatTitle);

Vue.use(VueRouter);
Vue.use(Vuex);
Vue.use(AMap);

Vue.filter("toCityName", function (code) {
  return toCityName(code);
});

Vue.filter("toDistrictName", function (code) {
  return toDistrictName(code);
});

Vue.filter("formatPrice", function (value) {
  if (value === undefined || value === null) return "";
  return "¥" + value.toFixed(2)
})

AMap.initAMapApiLoader({
  key: 'b04b292ba4b2140151e9c2bcd02bad0c',
  plugin: ['AMap.Geolocation', 'AMap.Autocomplete', 'AMap.PlaceSearch', 'AMap.Scale', 'AMap.OverView', 'AMap.ToolBar', 'AMap.MapType', 'AMap.PolyEditor', 'AMap.CircleEditor', 'AMap.CitySearch']
});

Vue.use(VueTouch, {
  name: 'v-touch'
})
VueTouch.config.swipe = {
  threshold: 100 //设置手指左右滑动距离
}

Vue.config.productionTip = false;

let store = initStore();

let router = new VueRouter({
  routes
});

router.beforeEach(function (to, from, next) {
  if (to.meta.auth) {
    if (store.state.user.loggedIn) {
      next();
    } else {
      let toastInstanse = Toast({
        message: "请先登录",
        position: "center",
        duration: 2000
      });
      toastInstanse.$el.style.zIndex = 10000000;
      next("/login");
    }
  } else {
    next();
  }
});

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')