
import data from "@/components/common/address.json";

const map = new Map();
for (let record1 of data) {
  map.set(record1.code, record1.name);
  for (let record2 of record1.childs) {
    map.set(record2.code, record2.name);
    for (let record3 of record2.childs) {
      map.set(record3.code, record3.name);
    }
  }
}

export function toProvinceName(code) {
  if (!code) return "";
  return map.get(code.substring(0, 2));
}

export function toCityName(code) {
  if (!code) return "";
  return map.get(code.substring(0, 4));
}

export function toDistrictName(code) {
  if (!code) return "";
  return map.get(code.substring(0, 6));
}

export function toAddress(contact) {
  let district = (contact) ? `${toCityName(contact.adcode)}/${toDistrictName(contact.adcode)}` : "填写城市/区域";
  let street = (contact) ? extractStreetName(contact.address, contact.adcode) : "请输入详细地址";
  return { district, street };
}


export function extractStreetName(address, code) {
  let province = toProvinceName(code);
  let city = toCityName(code);
  let district = toDistrictName(code);
  if (address.startsWith(province)) {
    address = address.substring(province.length);
  }
  if (address.startsWith(city)) {
    address = address.substring(city.length);
  }
  if (address.startsWith(district)) {
    address = address.substring(district.length);
  }
  return address;
}