<script>
import Vue from "vue";
import { Navbar, TabItem } from "mint-ui";
Vue.component(Navbar.name, Navbar);
Vue.component(TabItem.name, TabItem);
export default {
  data() {
    return {
      filters: {
        status: "全部"
      },
      orders: [
      ],
      search_key: "",
      optionList: ["全部", "待接单", "运输中", "待支付", "已完成", "取消/退款"],
    };
  },
  computed: {
    orders() {
      if (!this.filters.status) return this.$store.state.orders;
      return this.$store.state.orders;
    }
  },
  methods: {
    formatCargoInfo({ weight, volume, type }) {
      return `${weight}吨/${volume}方 ${type}`;
    }
  },

  async created() {
    await this.$store.dispatch("fetchOrders");
  }
};
</script>

