import axios from "axios";

import Vuex from "vuex";
import dayjs from "dayjs";

import order from "./order";
import user from "./user";

export function initStore() {

  let api = axios.create({
    // baseURL: "/api",
    baseURL: "http://consignor-api.manyi.oureway.com:8080"
  });

  let api2 = axios.create({
    // baseURL: "/api",
    baseURL: "http://consignor-api.manyi.oureway.com:8080"
  });


  let store = new Vuex.Store({
    modules: {
      order: order(api),
      user
    },
    state: {
      error: null,
      sessionId: localStorage.getItem("sessionId"),
      authToken: null,
      cargoSources: []
    },
    mutations: {
      setError(state, err) {
        state.error = err;
      },
      notifyError() {

      },
      setSessionId(state, value) {
        state.sessionId = value;
        localStorage.setItem("sessionId", value);
      },
      setAuthToken(state, value) {
        state.authToken = value;
      },
      setCargoSources(state, value) {
        state.cargoSources = value;
      },
      setOrders(state, value) {
        state.orders = value;
      }
    },
    actions: {
      async login({
        commit
      }, {
        mobile,
        verifyCode
      }) {
        console.log(">>", mobile, verifyCode)
        let {
          sessionId,
          loggedIn,
          message
        } = await api2.post("/login", {
          mobile,
          verifyCode
        }).then(res => res.data);
        if (loggedIn) {
          commit("setSessionId", sessionId);
        }
        commit("setLoggedIn", loggedIn);
        return {
          loggedIn,
          message
        };
      },

      // 计价
      async calculateFreight({
        commit
      }, params) {
        return api2.get("/freight-calc", {
          params: params
        }).then(res => res.data.price);
      },

      // 验证码
      async requestVerifyCode({
        commit
      }, {
        mobile
      }) {
        await api2.post("/verify-code", {
          mobile
        });
      },

      // 货源接口
      async fetchCargoSources({ commit, state }) {
        if (!state.authToken) return;
        let cargoSources = await api.get("/my/cargo-sources").then(res => res.data);
        commit("setCargoSources", cargoSources);
      },

      // 删除货源 
      async deleteCargoSource({ commit, state }, { id }) {
        await api.delete(`/my/cargo-sources/${id}`)
                  .catch(err => {
                    throw new Error("货源不存在")
                  });
        let cargoSources = state.cargoSources.filter(cs => cs.id != id);
        commit("setCargoSources", cargoSources);
      },

      

      async fetchOrders({ commit, state }) {
        let orders = await api.get(`/my/orders`).then(res => res.data);
        commit("setOrders", orders);
      },

      async cancelOrder({ commit }, { no }) {

      },

      async fetchUserProfile({ commit }) {
        let { nickname, mobile } = await api.get(`/user/profile`).then(res => res.data);
        commit("setNickname", nickname);
        commit("setMobile", mobile);
      }

    }
  });

  api.interceptors.request.use(async function (config) {
    let authToken;
    if (store.state.sessionId) {
      let needRefresh = true;
      if (store.state.authToken) {
        authToken = store.state.authToken;
        needRefresh = dayjs(store.state.authToken.expiresAt).subtract(2, "minute").isBefore(Date.now());
      }
      if (needRefresh) {
        authToken = await requestAuthToken(store.state.sessionId);
        // console.log("authToken", authToken);
        store.commit("setAuthToken", authToken);
      }
    }
    if (!config.headers) config.headers = {};
    if (authToken) config.headers["Authorization"] = `Bearer ${authToken.accessToken}`;
    return config;
  });

  async function requestAuthToken(sessionId) {
    let res = await api2.post("/auth", {
      sessionId,
      expiresIn: 10 * 60
    });
    return res.data;
  }

  return store;
}