<template>
  <div id="app">
    <router-view/>
  </div>
</template>


<script>
import { Toast } from "mint-ui";

export default {
  errorCaptured(err) {
    let toast = Toast({
      message: err.message,
      iconClass: 'icon icon-success',
      position: "middle",
      duration: 2000
    });
    toast.$el.style.zIndex = 100000;
  },
  created() {
    this.unsubscribe = this.$store.subscribe((mutation, state) => {
      if (mutation.type == "setConsignor" || mutation.type == "setConsignee") {
        if (state.order.consignor && state.order.consignee) {
          this.$store.dispatch("calcDistance");
        } else {
          this.$store.commit("clearDistance");
        }
      }
      if (mutation.type == "notifyError") {
        let toastInstanse = Toast({
          message: mutation.payload.message,
          position: "center",
          duration: 2000
        });
        toastInstanse.$el.style.zIndex = 10000000;
      }
    });
  },
  mounted() {
  },
  beforeDestroy() {
    this.unsubscribe();
  }
}
</script>


<style>
  @import url("assets/css/reset.css");
</style>
